import React,{useState} from 'react'
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import './App.css';
import data from './data'
import MovieDetails from './components/MovieDetails'
import Slider from './components/Slider';

function App() {
  const [movies, setMovies] = useState(data)
  const[movieId, setMovieId] = useState()
  const [url, setUrl] = useState()
  

  const filterMovie =(id)=>{
    const newMovie = movies.filter((item)=>item.id===id)    
    setMovieId(newMovie)
  }

  const detailHandler = (id)=>{
    setUrl(id)
    filterMovie(id)
  }

  console.log(url)

  return (
    <div className='App'>
    <h2>Employee List</h2>
    <div className='container'>  
       
      <Router>
      <Routes>
        <Route path='/' element={<Slider movies={movies} detailHandler={detailHandler} />} ></Route>
        <Route path={'movie/'+url} element={<MovieDetails movieId={movieId}  />}></Route>
      </Routes>
      </Router>
    </div>
    </div>
  );
}

export default App;
